.header {
    width: 100%;
    overflow: hidden;
    background-color: antiquewhite;
    padding: 8px 0;
}

.header__logo {
    display: flex;
    justify-content: flex-start;
    width: 120px;
}

.header__logo-img {    
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.header__title {
    display: flex;
    align-items: center;
}

.header__title-text {
    margin: 0;
    padding: 0;
}